* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container {
    display: flex;
    justify-content: center;
}
.cityInput {
    padding-left: 10px;
    height: 40px;
    border: 0px;
    transform: translate(0, -3%);
    color: white;
    width: 400px;
    background: transparent;
    font-size: 22px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.cityInput::placeholder {
    color: white;
    opacity: 0.6;
}
.searchBut {
    height: 40px;
    width: 60px;
    background: transparent;
    border: 0px;
    font-size: 20px;
    transform: translate(0, 5%);
    color: white;
}
.searchBut:hover {
    cursor: pointer;
}
.cityInput:focus {
    outline: none;
}
.inputComb {
    background: rgba(133, 246, 118, 0.507);
    background: linear-gradient(90deg, rgba(134, 246, 118, 1) 0%, rgba(106, 242, 69, 0.815) 53%, rgba(76, 207, 168, 0.835171568627451) 100%);
    margin-top: 20px;
    border-radius: 20px;
}
.suggested-cities {
    z-index: 1;
    position: relative;
    width: 460px;
    background: rgba(133, 246, 118, 0.507);
    background: linear-gradient(90deg, rgba(134, 246, 118, 1) 0%, rgba(106, 242, 69, 0.815) 53%, rgba(76, 207, 168, 0.835171568627451) 100%);
}