.box{
    margin-top: 30px;
    display: flex;
    justify-content: center;
}
.data{
    position: absolute;
    top: 100px;
    z-index: 0 ;
    height: 80vh;
    width: 170vh;
    background: rgb(134,246,118);
    background: linear-gradient(127deg, rgba(133, 246, 118, 0.733) 0%, rgba(107,242,69,0.9640231092436975) 43%, rgba(76,207,168,0.835171568627451) 100%);
    display: flex;
    border-radius: 30px;
}
.temp{
    background:transparent;
    height: 80vh;
    width: 110vh;
    padding-left: 15px;
}
.details{
    border-left: 3px solid white;
    background: transparent;
    height: 80vh;
    width: 60vh;
}
.tempHead{
    /* background-color: aqua; */
    text-align: center;
    
    margin-top: 40px;
    /* margin-left: 250px;  */
}
.heading{
    font-size:100px;
    color: white;
}
h3{
    font-size: 30px;
    color: white;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}
.cloudIcon{
    /* margin-top: 20px; */
    color: white;
    font-size: 70px;
}
.description{
    color: white;
    margin-top: 20px;
    font-size: 30px;
}
hr{
    border: 1px solid rgb(148, 148, 148) ;
    margin: auto;
}
.params-weather{
    list-style-type: none;
    text-align: center;
    margin-top: 50px;
}
.params-li{
    font-size: 20px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
    display: flex;
    justify-content: space-between;
    width: 270px;
    padding-left: 30px;
}